 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-apply-info-text {
	color: #fff;
	display: flex;
	justify-content: center;
	margin: 2rem;
	.info-container {
		background: white;
		color: black;
		padding: 2rem;
		border-radius: 18px;
		width: 100%;
		h3 {
			font-size: 48px;
			font-weight: bold;
			margin-bottom: 2rem;
		}
		p {
			font-size: 18px;
		}
	}
	@media screen and (min-width: 777px) {
		margin: 8rem;
	}
}

